import React from "react";
// TODO: import useFormik from formik library
import {useFormik} from 'formik';

function App() {
  // TODO: add a const called formik assigned to useFormik()
  const formik = useFormik({
    initialValues: {
      emailField: '',
      password: ''
    },
    onSubmit: values => {
      console.log('form: ', values);
      alert('Login Successful');
    },
    validate: values => {
      let errors = {};
      let validRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
      if (!values.emailField) errors.emailField = 'Field Required';
      if (!values.password) errors.password = 'Field Required';
      if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.emailField)) errors.emailField = 'Username should be an email'; 
      return errors;
    }
  });

  return (
     <div>
     <form onSubmit={formik.handleSubmit}>
      <div>Username</div>
      <input name='emailField' id="emailField" type='text' onChange={formik.handleChange} value={formik.values.emailField} />
      {formik.errors.emailField ? <div style={{color: 'red'}} id="emailError">{formik.errors.emailField}</div>: null}
      <div>Password</div>
      <input name='password' id="pswField" type='text' onChange={formik.handleChange} value={formik.values.password}/>
      {formik.errors.password ? <div style={{color: 'red'}} id="pswError">{formik.errors.password}</div>: null}
      <button type='submit' id="submitBtn">Submit</button>

     </form>
    </div>
  );
}

export default App;
